	module.exports = {"data":{"site":{"siteMetadata":{"title":"Blog by noboru-i","subtitle":"noboru-iのメモ的な何かです。","copyright":"© All rights reserved.","author":{"name":"noboru-i","twitter":"noboru_i"},"disqusShortname":"","url":"https://noboru-i.github.io/"}},"markdownRemark":{"id":"/home/circleci/code/src/pages/articles/2018/2018-06-01.md absPath of file >>> MarkdownRemark","html":"<h2>React NativeをWebに持ってくることの意味 - ナカザンドットネット</h2>\n<p><a href=\"http://blog.nkzn.info/entry/2018/05/29/210030\">http://blog.nkzn.info/entry/2018/05/29/210030</a></p>\n<ul class=\"post-single__tags-list\">\n  <li class=\"post-single__tags-list-item\">\n    <a class=\"post-single__tags-list-item-link\" href=\"/tags/react native/\">React Native</a>\n  </li>\n</ul>\n<p>React Native for Webとは何なのか、をベースに、ブラウザでのアプリケーション開発の辛さの言語化。</p>\n<p>個人的には、ReactNativeで書いたアプリを、そのままブラウザでレンダリングしてほしかったけど、そういうものでは無さそう？\n改めて、調べてみよう。</p>\n<h2>Android Testは\u000b“Write once, run everywhere”\u000bの夢を見るか？ - Google スライド</h2>\n<p><a href=\"https://docs.google.com/presentation/d/1M_WMloMI18MNUYBSx9xHH3HiGktZIcTk4gG1PPfD_mg/edit#slide=id.p\">https://docs.google.com/presentation/d/1M<em>WMloMI18MNUYBSx9xHH3HiGktZIcTk4gG1PPfD</em>mg/edit#slide=id.p</a></p>\n<ul class=\"post-single__tags-list\">\n  <li class=\"post-single__tags-list-item\">\n    <a class=\"post-single__tags-list-item-link\" href=\"/tags/android/\">Android</a>\n  </li>\n</ul>\n<p>高速なテストと、信頼性の高いテストを、同じ記述で実行できるっぽい。良さげ。</p>\n<h2>ユーザ情報を保存する時のテーブル設計 - そーだいなるらくがき帳</h2>\n<p><a href=\"https://soudai.hatenablog.com/entry/2018/05/01/204442\">https://soudai.hatenablog.com/entry/2018/05/01/204442</a></p>\n<ul class=\"post-single__tags-list\">\n  <li class=\"post-single__tags-list-item\">\n    <a class=\"post-single__tags-list-item-link\" href=\"/tags/設計/\">設計</a>\n  </li>\n</ul>\n<p>いろんなパターンの詳細情報を持つテーブルの設計方法について、一つの指針。</p>\n<p>たしかに、usersというidだけを管理するテーブルを作って、 user_detail に情報をもたせるのは良さそう。（ロック周り、あんまり詳しくないけど）</p>\n<p>ユーザの有効・無効などの状態は悩みどころですよね。\n毎回 JOIN するのも面倒・漏れが出たりするし、管理画面ではそれを除外して全件見たかったり。</p>\n<h2>【Android】Google IO 2018で新発表された navigation についての詳細レポート - DMM inside</h2>\n<p><a href=\"https://inside.dmm.com/entry/2018/05/25/android-navigation\">https://inside.dmm.com/entry/2018/05/25/android-navigation</a></p>\n<ul class=\"post-single__tags-list\">\n  <li class=\"post-single__tags-list-item\">\n    <a class=\"post-single__tags-list-item-link\" href=\"/tags/android/\">Android</a>\n  </li>\n</ul>\n<p>Architecture Componentに新たに仲間入りした、Navigationについての記事。</p>\n<p>Fragmentでの画面遷移に悪い思い出しかないけど、もうちょっと安定したら使ってみようかと思います。\nいまのところ、まだalphaですし、不具合が多そうな印象。</p>","fields":{"tagSlugs":["/tags/react-native/","/tags/android/","/tags/設計/"]},"frontmatter":{"title":"Weekly news at 2018/06/01","tags":["React Native","Android","設計"],"date":"2018-06-01T17:01:25+00:00","description":"2018/06/01のウィークリーニュース"}}},"pathContext":{"slug":"/posts/weekly-news/2018/06/01/"}}

